body {
  color: #fff;
  margin: 0;
}

.content {
  padding-bottom: 3em;
}

:root {
  --column-left: 7%;
  --column-width: 89%;
  hyphens: auto;
  --color-bg: #001168;
  --color-one: #1faef579;
  --color-two: #5ae4e486;
  --color-three: #ffa600;
  --color-signal: #0af;
  font-family: Work Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

@media (width >= 768px) {
  :root {
    --column-left: 25%;
    --column-width: 60%;
    font-size: 21px;
  }
}

@media (width >= 1100px) {
  :root {
    --column-left: 30%;
    --column-width: 50%;
    font-size: 21px;
  }
}

p {
  margin-top: 0;
  margin-bottom: .7em;
}

h1, h2, h3, h4 {
  margin-top: .9em;
